import React from "react";
import "../App.css"
import StylisedButton from "../components/StylisedButton";
import { BrowserView, MobileView, isBrowser, isMobile } from 'react-device-detect';

function Home(props){

    const navToStage = () => {
        props.navigate("Stage")
    }
    const navToDemo = () => {
        window.location.href = "https://youtu.be/tjNBEfQdDFE"
    }

    return (
        <div className="Container">
            <BrowserView>
            <div className="Container">
            <div className="Note1" />
            <div className="Note2" />
            <div className="TopBanner">MikuProCon24</div>
            <div className="TitleText" />
            <div className="MakuhariSilhouette" />
            <div className="HomeButtonContainer">
                <StylisedButton text="Begin!" style="PrimaryColor" onClick={navToStage}/>
                <StylisedButton text="Watch Demo!" style="SecondaryColor" onClick={navToDemo}/>
            </div>
            </div>
            </BrowserView>
            <MobileView>
            <div className="Container MobileErrorMessage">
                <div className="InfoIcon"/>
                <p>Hi, it looks like you're trying to open this page on a phone. <br/> This is the desktop portion of the application, so please open it on either a desktop or a laptop!</p>
                <p><br/></p>
                <p>{window.location.href}</p>
            </div>
            </MobileView>
        </div>
    )
}

export default Home