import React, { useEffect, useState } from "react";
import "../App.css";
import "./Scene.css"
import {initializePlayer, play} from "../TextAliveApi";
import video01 from '../videos/01_fhd.mp4';
import video02 from '../videos/02_fhd.mp4';
import video03 from '../videos/03_fhd.mp4';
import video04 from '../videos/04_fhd.mp4';
import video05 from '../videos/05_fhd.mp4';
import video06 from '../videos/06_fhd.mp4';
import video07 from '../videos/07_fhd.mp4';

function Scene(props) {

    const videos = [ video01 , video02 , video03 , video04 , video05 , video06 , video07 ]
    const [currentVideoIndex, setCurrentVideoIndex] = useState(0)
    const [string, setString] = useState("");
    const [externalString, setExternalString] = useState("");
    const [intervalId, setIntervalId] = useState(null);
    var startPlaybackTimeout = null 

    var navigateToHome = () => {
        clearTimeout(startPlaybackTimeout)
        props.navigateToHome()
    }

    function calculateHeightBasedOnAspectRatio(currentAspectRatio) {
        const minValue = 19;
        const maxValue = 35;
        const minAspectRatio = 1.5;
        const maxAspectRatio = 2;
        const value = maxValue - (maxValue - minValue) * (currentAspectRatio - minAspectRatio) / (maxAspectRatio - minAspectRatio);
        return value;
    }

    var curvedTextHeight = calculateHeightBasedOnAspectRatio(window.innerWidth/window.innerHeight);

    const switchVideo = () => {
        setCurrentVideoIndex( currentVideoIndex => (currentVideoIndex+1) % videos.length )
    }

    useEffect(()=>{
        setString(externalString)
    },
    [externalString])

    useEffect(() => {
        if (intervalId) {
          clearInterval(intervalId);
        }
    
        const newIntervalId = setInterval(() => {
          setString(prevString => {
            if (prevString.length === 1) {
              clearInterval(newIntervalId);
              return prevString;
            }
            return prevString.slice(1);
          });
        }, 390);
    
        setIntervalId(newIntervalId);
    
        return () => clearInterval(newIntervalId);
      }, [string]);

    useEffect(()=>{
        if(parseInt(props["startIn"])>0)
            {
                console.log("Starting in " , parseInt(props["startIn"]))
                startPlaybackTimeout = setTimeout(
                    () => {
                        console.log("PLAYING")
                        play()
                    },
                    parseInt(props["startIn"])
                )
            }
        else{
            console.log("Time Out is 0")
        }
    },[props])

    useEffect(()=>{        
        try{
            initializePlayer(setExternalString)
        }
        catch(error){
            console.error(error)
        }

        setInterval( switchVideo , 10000 );

    },[])

    return <div className="SceneContainer" >
        <video src={videos[currentVideoIndex]} height="120%" width="120%" autoPlay loop muted/>
        <div className="TextContainer">
                <div key={externalString} style={{height: curvedTextHeight+"%" }} className={"CurvedTextContainer" + (externalString.includes("") ? " LowOpacity" : "")}>
                    {string.length > 0 && externalString}
                </div>            
                                                                                   {/* "" breaks the tracking as it looks similar to the tracking marker. */}
                <div key={"R"+string.charAt(0)} className={"MainTextContainer Right" + ("" == string.charAt(0) ? " LowOpacity" : "")}>
                    {string.charAt(0)}
                </div>            
                <div key={"L"+string.charAt(0)} className={"MainTextContainer Left" + ("" == string.charAt(0) ? " LowOpacity" : "")}>
                    {string.charAt(0)}
                </div>            
        </div>
        <div className="StageBG"></div>
        <div className="StageBackButton" onClick={ navigateToHome }>Back to Home</div>
    </div>
}

export default Scene