import React, { useEffect, useState } from "react";
import "../App.css";
import Home from "./Home";
import Stage from "./Stage";
import AR from "./AR";
import { getTargetTime } from "../Util";
import Demo from "./Demo";

function Hub(){

  const [location, setLocation] = useState("")
  var targetTime = getTargetTime()

  if(location=="Stage")
  {
    return <Stage navigate={setLocation} currentLocation={location}/>
  }
  else if(targetTime != null)
  {
    return <AR />
  }
  else {
    return <Home navigate={setLocation}/>
  }

}

export default Hub