
import { Player } from "textalive-app-api";


const animateWord = function (now, unit) {
  if (unit.contains(now)) {
    console.log(unit.text)
    _updateDisplayText(unit.text)
  }
};

var player = null;
var _updateDisplayText = () => {}

function initializePlayer(updateDisplayText) {

        _updateDisplayText = updateDisplayText

        player = new Player({
            app: {
            token: "n3DBt0f3zoNmpGVB",
            },
            mediaElement: document.querySelector("#media"),
        });
        
        player.addListener({
            onAppReady,
            onVideoReady,
            onTimerReady,
            onThrottledTimeUpdate,
            onPlay,
            onPause,
            onStop,
        });
}

function play(){
        if (player) {
          if (player.isPlaying) {
            player.requestPause();
          } else {
            player.requestPlay();
          }
        }
        return false;
}

function stop(){
  if (player) {
    console.log("STOPPING")
    player.requestStop();
  }
}

const artistSpan = document.querySelector("#artist span");
const songSpan = document.querySelector("#song span");

function onAppReady(app) {

  if (!app.managed) {
    
  } else {
    
  }

  
  try {
    if (!app.songUrl) {
        
      //  / 
      player.createFromSongUrl("https://piapro.jp/t/--OD/20240202150903", 
        {
        video: {
           beatId: 4592296,
           chordId: 2727636,
           repetitiveSegmentId: 2824327,
           lyricId: 59416,
           lyricDiffId: 13963
         },
       }
    );

    }
  } catch (error) {
        console.log(error)
  }

}

function onVideoReady(v) {

  let w = player.video.firstWord;
  while (w) {
    w.animate = animateWord;
    w = w.next;
  }
}

