import './App.css';
import React, { useState, useEffect, useRef } from 'react'
import ReactDOM from "react-dom/client";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import NET from 'vanta/dist/vanta.net.min.js'
import Hub from './pages/Hub';

function App() {  
  const [vantaEffect, setVantaEffect] = useState(null)
  const myRef = useRef(null)

  const logError = (err) => {
    console.log( "ERROR : " , err )
    return false; 
  }

  useEffect(() => {

    window.onerror = logError;

    if (!vantaEffect) {
      setVantaEffect(NET({
        el: myRef.current,
        mouseControls: false,
        touchControls: false,
        gyroControls: false,
        minHeight: 200.00,
        minWidth: 200.00,
        scale: 1.00,
        scaleMobile: 1.00,
        color: 0xffffff,
        backgroundColor: 0x9dcbff,
        spacing: 20.00,
        maxDistance: 25.00,
        showDots: false
      }))
    }
    return () => {
      if (vantaEffect) vantaEffect.destroy()
    }
  }, [vantaEffect])

  return (
    <div className="App" ref={myRef}>
      <BrowserRouter basename='arstage'>
      <Routes>
        <Route path="/" element={<Hub />
      
      } />
      </Routes>
    </BrowserRouter>
    </div>
  );
}

export default App;
